import PressCell from 'press-ui/press-cell/press-cell.vue';
import PressStepper from 'press-ui/press-stepper/press-stepper.vue';
import PressToast from 'press-ui/press-toast/press-toast.vue';


export default {
  i18n: {
    'zh-CN': {
      step: '步长设置',
      range: '限制输入范围',
      integer: '限制输入整数',
      roundTheme: '圆角风格',
      asyncChange: '异步变更',
      customSize: '自定义大小',
      disableInput: '禁用输入框',
      decimalLength: '固定小数位数',
      closeLongPress: '关闭长按',
    },
    'en-US': {
      step: 'Step',
      range: 'Range',
      integer: 'Integer',
      roundTheme: 'Round Theme',
      asyncChange: 'Async Change',
      customSize: 'Custom Size',
      disableInput: 'Disable Input',
      decimalLength: 'Decimal Length',
      closeLongPress: 'Close Long Press',
    },
  },

  components: {
    PressStepper,
    PressToast,
    PressCell,
  },
  data() {
    return {
      value: 1,
      asyncValue: 1,
    };
  },
  methods: {
    onChange(val) {
      console.log('onChange.val', val);
    },
    onSyncChange(val) {
      this.$toast.loading({
        duration: 1000,
      });
      // QQ小程序不能使用空字符串
      // this.onGShowLoading(' ', {
      //   duration: 1000,
      // });
      setTimeout(() => {
        console.log('onSyncChange.val', val);
        this.asyncValue = val;
      }, 1000);
    },

  },
};
